:root {
  --color-navbar: #c6d3ec;
  --color-green-light: #7cb2c7;
  --color-green-dark: #90b7c7;
  --text-color: #676fa3;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Pushster", cursive;
  background: linear-gradient(
    to right bottom,
    var(--color-green-light),
    var(--color-green-dark)
  );
  min-height: 100vh;
  background-repeat: no-repeat;
  @media (max-width: 768px) {
    overflow-x: hidden;
  }
}
