.nav {
  height: 8vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-navbar);
  position: relative;
  transition: transform 1s;

  .logo {
    width: 5rem;
    height: 5rem;
    margin-left: 5rem;
    fill: #35589a;
  }
  .nav__links {
    @media (max-width: 768px) {
      background-color: #548cff;
      position: absolute;
      right: 0;
      height: 92vh;
      top: 8vh;
      display: flex;
      flex-direction: column;
      width: 50%;
      margin-right: 0;
      align-items: center;
      justify-content: space-evenly;
      transform: translateX(100%);
      transition: transform 1s;
      overflow-x: hidden;
    }
    font-size: 1.6rem;
    display: flex;
    margin-right: 5rem;
    .nav__items {
      list-style-type: none;
      &:not(:last-child) {
        margin-right: 4rem;
        @media (max-width: 768px) {
          margin-right: 0;
        }
      }
    }
    .nav__link {
      &:link,
      &:visited {
        color: var(--text-color);
        text-decoration: none;
      }
    }
  }
  .burger-menu {
    display: none;
    cursor: pointer;
    flex-basis: 2.5rem;
    position: relative;
    @media (max-width: 768px) {
      display: block;
    }
    .line {
      width: 100%;
      height: 0.5rem;
      margin: 0.5rem;
      background-color: red;
      transition: transform 0.5s;
    }
    margin-right: 4rem;
  }

  .rotate {
    height: 3em;
  }
  .rotate .line-2 {
    display: none;
  }
  .rotate .line-1 {
    transform: translateY(1rem) rotate(45deg);
  }
  .rotate .line-3 {
    transform: rotate(-45deg);
  }
  .showmenu {
    @media (max-width: 768px) {
      transform: translateX(0%);
      overflow-x: hidden !important;
    }
  }
}
